import PropTypes from 'prop-types';
import React, { forwardRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinus, faPlus } from '@fortawesome/pro-solid-svg-icons';
import { faChartNetwork } from '@fortawesome/pro-duotone-svg-icons';

import Button from '../../buttons/rectangular/button';
import ButtonIcon from '../../buttons/icon/button';

import './network.css';

const Network = forwardRef((
  {
    handleReset,
    handleUnselect,
    handleZoomIn,
    handleZoomOut,
  },
  ref,
) => (
  <section className="display network">
    <div className="display__inner">
      <h2
        className="display__title"
        id="network"
      >
        <FontAwesomeIcon icon={faChartNetwork} />
        Network
      </h2>
      <p className="display__legend network__legend">
        Network view of the viral proteins with their (at most) top twenty-five preys. Baits
        are in
        {' '}
        <span className="network__legend_purple">Purple</span>
        {' '}
        and previoulsy reported interactions are shown as
        {' '}
        <span className="network__legend_orange">Orange</span>
        {' '}
        lines.
      </p>
      <p className="display__instructions">
        Use the mouse to zoom and pan on the network. Click a node to highlight its
        interaction partners.
      </p>
      <div
        className="network__cytoscape-container"
        ref={ref}
      />
      <div className="network__buttons">
        <ButtonIcon
          ariaLabel="Zoom in"
          icon={faPlus}
          kind="primary"
          onClick={handleZoomIn}
        />
        <ButtonIcon
          ariaLabel="Zoom out"
          icon={faMinus}
          kind="primary"
          onClick={handleZoomOut}
        />
        <Button
          kind="primary"
          onClick={handleUnselect}
        >
          Clear
        </Button>
        <Button
          kind="warning"
          onClick={handleReset}
        >
          Reset
        </Button>
      </div>
    </div>
  </section>
));

Network.propTypes = {
  handleReset: PropTypes.func.isRequired,
  handleUnselect: PropTypes.func.isRequired,
  handleZoomIn: PropTypes.func.isRequired,
  handleZoomOut: PropTypes.func.isRequired,
};

export default Network;
