import cytoscape from 'cytoscape';
import React, { useRef, useEffect } from 'react';
import { cloneDeep } from 'lodash';

import Network from './network';

import useFetch from '../../hooks/fetch/use-fetch';

const ZOOM_INCREMENT = 0.1;
const ZOOM_MIN = 0.1;

const NetworkContainer = () => {
  const ref = useRef();
  const { fetching, response: network } = useFetch('/files/network.json');

  const cy = network
    ? cytoscape({
      container: ref.current,
      elements: cloneDeep(network.elements),
      layout: {
        name: 'preset',
      },
      minZoom: ZOOM_MIN,
      style: network.style,
    })
    : null;

  const handleReset = () => {
    cy.elements().remove();
    cy.add(cloneDeep(network.elements));
    cy.fit([], 30);
  };

  const handleUnselect = () => {
    cy.$('*').neighborhood().removeClass('hidden');
  };

  const handleZoomIn = () => {
    const currentZoom = cy.zoom();
    const rect = ref.current.getBoundingClientRect();
    cy.zoom({
      level: currentZoom + ZOOM_INCREMENT,
      renderedPosition: { x: rect.left + (rect.width / 2), y: rect.top + (rect.height / 2) },
    });
    cy.center();
  };

  const handleZoomOut = () => {
    const currentZoom = cy.zoom();
    cy.zoom({
      level: Math.max(currentZoom - ZOOM_INCREMENT, ZOOM_MIN),
    });
    cy.center();
  };

  const startingZoom = cy ? cy.zoom() : 1;

  useEffect(() => {
    if (cy) {
      cy.on('zoom', () => {
        const zoomFactor = cy.zoom() / startingZoom;
        // console.log(zoomFactor);
      });
      cy.on('tap', 'node', (e) => {
        cy.$('*').neighborhood().addClass('hidden');
        cy.$(`#${e.target.id()}`).removeClass('hidden');
        cy.$(`#${e.target.id()}`).neighborhood().removeClass('hidden');
      });
    }
  }, [cy]);

  return (
    <Network
      handleReset={handleReset}
      handleUnselect={handleUnselect}
      handleZoomIn={handleZoomIn}
      handleZoomOut={handleZoomOut}
      isLoading={fetching}
      ref={ref}
    />
  );
};

export default NetworkContainer;
